body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
  background-color: #f8f9fa;
  flex-direction: column;
  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  display: flex;
}

.App-main-no-navbar {
  flex: 1;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.search-container-no-navbar {
  text-align: left;
  width: 100%;
}

.search-input {
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 6px;
  width: 100%;
  margin-bottom: 20px;
  padding: 12px 16px;
  font-size: 16px;
  transition: border-color .2s, box-shadow .2s;
}

.search-input:focus {
  border-color: #667eea;
  outline: none;
  box-shadow: 0 0 0 3px #667eea33;
}

.cards-list {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.simple-card {
  cursor: pointer;
  background: #fff;
  border: 1px solid #e1e4e8;
  border-radius: 6px;
  padding: 15px 20px;
  transition: transform .2s, box-shadow .2s;
  box-shadow: 0 2px 6px #0000001a;
}

.simple-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 10px #00000026;
}

.simple-card-title {
  color: #24292e;
  margin: 0;
  font-size: 1.1em;
  font-weight: 500;
}

.no-results {
  text-align: center;
  color: #666;
  padding: 30px 20px;
  font-size: 1.1em;
}

.winbox-content {
  color: #fff;
  height: 100%;
  padding: 20px;
  overflow-y: auto;
}

.winbox-content p {
  margin-bottom: 15px;
  line-height: 1.6;
}

mark {
  color: #000;
  background-color: #ffeb3b;
  border-radius: 2px;
  padding: 0 2px;
  font-weight: 600;
}

.App-footer {
  background-color: #f6f8fa;
  border-top: 1px solid #e1e4e8;
  margin-top: auto;
  padding: 15px 20px;
}

.App-footer p {
  color: #666;
  margin: 0;
  font-size: 14px;
}

.App-footer code {
  color: #d73a49;
  background-color: #f1f3f4;
  border-radius: 3px;
  padding: 2px 6px;
  font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
}

@media (width <= 768px) {
  .App-main-no-navbar {
    padding: 15px;
  }

  .simple-card {
    padding: 12px 16px;
  }

  .simple-card-title {
    font-size: 1em;
  }
}
